import React, { useState, useEffect } from 'react';
import './App.scss';

// Components
import Atm from './components/Atm';

const App: React.FC = () => {
    const [saldo, setSaldo] = useState<number | undefined>(25000);

    const handleSaque = (valor: string) => {
        fetch("http://vrp_atm/HandleSaque", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({
                valor: valor
            })
        })
        .then(res => res.json())
        .then(cb => {
            setSaldo(cb.saldo);
        });
    }

    const handleTransfer = (valor: string, target: string) => {
        fetch("http://vrp_atm/HandleTransfer", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({
                valor: valor,
                target: target
            })
        })
        .then(res => res.json())
        .then(cb => {
            setSaldo(cb.saldo);
        });
    }

    const handleEvent = (event:any) => {
        if (event === undefined) return;
        const message = event.data;

        switch(message.action) {
            case "show":
                setSaldo(message.saldo);
                (document.getElementById("root") as HTMLDivElement).style.display = "block";
                break;
        }
    }
    useEffect(() => {
        window.addEventListener("message", handleEvent);
        window.onkeyup = (data:any) => {
            if (data.which === 27) {
                (document.getElementById("root") as HTMLDivElement).style.display = "none";
                fetch("http://vrp_atm/CloseApp", {
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "POST",
                    body: JSON.stringify({})
                })
                .catch(err => console.log(err));
            }
        };
        return () => {
            window.removeEventListener("message", handleEvent);
        }
    }, [handleEvent]);

    return (
        <div className="App">
            <Atm
                saldo={saldo}
                handleSaque={handleSaque}
                handleTransfer={handleTransfer}
            />
        </div>
    );
}

export default App;
