import React, { useState } from 'react';
import './styles.scss';

// Assets
import Logo from '../../assets/images/fleeca.png';

// Material
import { IconButton, Button, Tooltip, Typography, Card, CardActions, CardContent,
    Dialog, DialogTitle, DialogActions, DialogContent, TextField, Divider } from '@material-ui/core';
import { Close, LocalAtmSharp, SyncAltSharp } from '@material-ui/icons';

interface Props {
    saldo: number | undefined;
    handleSaque: (valor: string) => void;
    handleTransfer: (valor: string, target: string) => void;
}

const Atm: React.FC<Props> = ({ saldo, handleSaque, handleTransfer }) => {
    const [valor, setValor] = useState<string | undefined>();
    const handleChangeValor = (e: React.ChangeEvent<HTMLInputElement>) => {
        setValor(e.target.value);
    }
    const [target, setTarget] = useState<string | undefined>();
    const handleChangeTarget = (e: React.ChangeEvent<HTMLInputElement>) => {
        setTarget(e.target.value);
    }

    // Sacar
    const [sacar, setSacar] = useState<boolean>(false);
    const closeSacar = () => {
        setSacar(false);
        setValor(undefined);
    }
    const doSacar = () => {
        setSacar(false);
        if (valor === undefined) return
        handleSaque(valor);
        setValor(undefined);
    }

    // Transferir
    const [transfer, setTransfer] = useState<boolean>(false);
    const closeTransfer = () => {
        setTransfer(false);
        setValor(undefined);
        setTarget(undefined);
    }
    const doTransferir = () => {
        setTransfer(false);
        if (valor === undefined || target === undefined) return
        handleTransfer(valor,target);
        setTarget(undefined);
        setValor(undefined);
    }

    const doClose = () => {
        (document.getElementById("root") as HTMLDivElement).style.display = "none";
        fetch("http://vrp_atm/CloseApp", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({})
        })
        .catch(err => console.log(err));
    }

    return (
        <div id="Atm">
            <div className="header">
                <img src={Logo} alt="Logo Fleeca" />
                <Tooltip title="Fechar" arrow>
                    <IconButton onClick={doClose}>
                        <Close style={{fill: "#30C776"}} />
                    </IconButton>
                </Tooltip>
            </div>
            <Card square className="saldoContainer">
                <CardContent>
                    <Typography variant="button">
                        Saldo Dispon√≠vel
                    </Typography>
                    <Typography style={{color: "#1F972D"}}>
                        {saldo !== undefined ? Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD'}).format(saldo) : ''}
                    </Typography>
                </CardContent>
                <Divider />
                <CardActions style={{justifyContent: "space-evenly"}}>
                    <Button
                        startIcon={<LocalAtmSharp />}
                        onClick={() => setSacar(true)}
                        style={{color: "#30C776"}}
                    >
                        sacar
                    </Button>
                    <Button
                        startIcon={<SyncAltSharp />}
                        onClick={() => setTransfer(true)}
                        style={{color: "#30C776"}}
                    >
                        transferir
                    </Button>
                </CardActions>
            </Card>

            <Dialog
                open={sacar}
                onClose={closeSacar}
                container={() => document.getElementById("Atm")}
                style={{position: 'absolute'}}
                BackdropProps={{ style: { position: 'absolute' } }}
                fullWidth
            >
                <DialogTitle>Sacando</DialogTitle>
                <Divider />
                <DialogContent>
                    <TextField
                        value={valor}
                        label="Valor"
                        onChange={handleChangeValor}
                        style={{color: "#30C776"}}
                        fullWidth
                    />
                </DialogContent>
                <DialogActions style={{ justifyContent: 'space-between', padding: '16px'}}>
                        <Button
                            style={{color: "#30C776"}}
                            onClick={doSacar}
                        >
                            Sacar
                        </Button>
                        <Button
                            onClick={closeSacar}
                        >
                            Cancelar
                        </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={transfer}
                onClose={closeTransfer}
                container={() => document.getElementById("Atm")}
                style={{position: 'absolute'}}
                BackdropProps={{ style: { position: 'absolute' } }}
                fullWidth
            >
                <DialogTitle>Transferindo</DialogTitle>
                <Divider />
                <DialogContent>
                    <TextField
                        value={target}
                        label="Conta"
                        onChange={handleChangeTarget}
                        style={{color: "#30C776"}}
                        fullWidth
                    />
                    <TextField
                        value={valor}
                        label="Valor"
                        onChange={handleChangeValor}
                        style={{color: "#30C776"}}
                        fullWidth
                    />
                </DialogContent>
                <DialogActions style={{ justifyContent: 'space-between', padding: '16px'}}>
                        <Button
                            style={{color: "#30C776"}}
                            onClick={doTransferir}
                        >
                            Transferir
                        </Button>
                        <Button
                            onClick={closeTransfer}
                        >
                            Cancelar
                        </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default Atm;